.workout .container {
  display: grid;
  grid-template-columns: auto 52.93vw;
  padding-top: 120px;
  padding-bottom: 120px;
  gap: 20px;

  @include vp-1023 {
    grid-template-columns: 29.13vw 52.93vw;
    justify-content: space-between;
  }

  @include vp-767 {
    display: flex;
    flex-direction: column;
    padding-top: 101px;
    padding-bottom: 100px;
  }

  @include vp-319 {
    padding-top: 66px;
    gap: 0;
    padding-bottom: 69px;
  }
}

.workout__desc-block {
  margin-left: 60px;
  padding-top: 41px;

  @include vp-1023 {
    margin-left: 0;
  }

  @include vp-767 {
    margin-left: 0;
    padding-top: 0;
  }

  @include vp-319 {
    margin-bottom: 15px;
  }
}

.workout__title {
  font-size: 3.54vw;
  font-weight: 600;
  line-height: normal;
  letter-spacing: 1.44px;
  margin-bottom: 11px;

  @include vp-767 {
    font-size: 42px;
    letter-spacing: 1.26px;
    padding-right: 14px;
    text-align: center;
    margin-bottom: 2px;
  }

  @include vp-319 {
    font-size: 9.9vw;
    padding: 0;
    text-align: left;
    margin-bottom: 7px;
  }
}

.workout__caption {
  font-size: 1.12rem;
  font-weight: 500;
  line-height: 150%;
  margin-bottom: 40px;

  @include vp-767 {
    text-align: center;
    margin-bottom: 35px;
    font-size: 18px;
  }

  @include vp-319 {
    text-align: left;
    padding: 0;
    margin-bottom: 25px;
  }
}

.workout__text {
  font-size: 0.9rem;
  font-weight: 400;
  line-height: 24px;

  @include vp-767 {
    text-align: center;
    padding: 0 7vw;
    margin-bottom: 22px;
    font-size: 0.91rem;
  }

  @include vp-319 {
    text-align: left;
    padding: 0 8vw 0 0;
    margin-bottom: 17px;
  }
}

.workout__text:last-child {
  margin-top: 7px;

  @include vp-767 {
    display: none;
  }
}

.workout__preview {
  position: relative;

  picture {
    display: block;
    margin-left: 64px;
  }

  @include vp-767 {

    picture {
      margin-left: 0;
    }
  }

  @include vp-319 {
    picture {
      width: 100vw;
      margin-left: -15px;
      margin-bottom: 40px;
    }
  }
}

.workout__video {
  position: relative;
  max-width: 364px;
  height: 228px;
  margin-top: -8.19vw;

  iframe {
    max-width: 364px;
    height: inherit;
    border: none;
  }

  @include vp-767 {
    margin: -10.82vw auto 0 auto;
    max-width: 266px;
    height: 167px;

    iframe {
      max-width: 265px;
    }
  }

  @include vp-319 {
    width: 100vw;
    max-width: none;
    margin: 0 0 0 -15px;

    iframe {
      max-width: none;
      width: 100vw;
    }
  }
}

.workout__video[data-is-play="false"] {
  &::after {
    position: absolute;
    content: '';
    width: 100%;
    height: 100%;
    background-image: url("../img/main/preview-desk.jpg");
    left: 0;
  }

  div {
    width: 100px;
    height: 100px;
    position: absolute;
    z-index: 3;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    cursor: pointer;

    svg {
      circle {
        transition: all 0.3s ease;
      }

      path {
        transition: all 0.3s ease;
      }

      stop {
        transition: all 0.3s ease;
      }
    }

    @include hover {
      .play-circle-1 {
        stroke-opacity: 1;
      }
      .play-circle-2 {
        fill: #FFFFFF;
      }
      .play-path {
        fill: $color-default-sapphire;
      }

      svg {
        stop {
          stop-color: #102654;
        }
      }
    }

    @include focus {
      outline: none;
      .play-circle-1 {
        stroke-opacity: 1;
      }
      .play-circle-2 {
        fill: #FFFFFF;
      }
      .play-path {
        fill: $color-default-sapphire;
      }

      svg {
        stop {
          stop-color: #102654;
        }
      }
    }

    @include active {
      .play-circle-1 {
        stroke-opacity: 1;
      }
      .play-circle-2 {
        fill: #FFFFFF;
      }
      .play-path {
        fill: $color-default-sapphire;
      }

      svg {
        stop {
          stop-color: #102654;
        }
      }
    }
  }

  &::before {
    position: absolute;
    content: '';
    width: 100%;
    height: 100%;
    left: 0;
    z-index: 2;
    background: rgba(20, 54, 122, 0.50);
  }

  @include vp-767 {
    &::after {
      background-image: url("../img/main/preview-tablet.jpg");
    }

    div {
      transform: scale(0.7);
    }
  }

  @include vp-319 {
    &::after {
      background-image: url("../img/main/preview-mobile.jpg");
    }
  }
}

.workout__video[data-is-play="true"] {
  div {
    display: none;
  }

  &::before {
    display: none;
  }

  &::after {
    display: none;
  }
}
