.contacts {
  flex: 1;
  border-top: 1px solid #74819C;
}

.contacts__title {
  font-size: 54px;
  font-weight: 600;
  line-height: normal;
  margin-bottom: 43px;
  letter-spacing: 1.62px;

  @include vp-767 {
    letter-spacing: 1px;
    font-size: 42px;
    margin-bottom: 37px ;
  }

  @media (max-width: 550px) {
    font-size: 32px;
    margin-bottom: 38px;
  }
}

.contacts__wrap {
  padding: 4.40vw 11.65vw 4.40vw 8.79vw;

  @media (max-width: 1120px) {
    padding: 70px 21.25vw 70px 17.34vw;
  }

  @include vp-767 {
    padding-top: 63px;
    padding-bottom: 65px;
  }

  @media (max-width: 550px) {
    padding: 65px 15px;
  }
}

.contacts__grid {
  display: grid;
  grid-template-columns: auto auto;
  padding-left: 4px;
  row-gap: 19px;
  column-gap: 8.35vw;
  position: relative;

  &::before {
    content: '';
    position: absolute;
    height: 100%;
    width: 1px;
    background-color: #74819C;
    left: 64px;
    right: 0;
    margin: 0 auto;
  }

  h3 {
    font-family: $ff-roboto;
    font-size: 0.86rem;
    font-style: normal;
    font-weight: 700;
    line-height: 150%;
    margin-bottom: 2px;
  }

  p {
    font-size: 0.83rem;
    font-style: normal;
    font-weight: 400;
  }

  @include vp-767 {
    padding-left: 0;
    row-gap: 23px;
    column-gap: 9.13vw;

    &::before {
      left: 8.21vw;
    }

    h3 {
      font-size: 1.1rem;
      margin-bottom: 8px;
    }

    p {
      font-size: 1.04rem;
    }
  }

  @media (max-width: 550px) {
    display: flex;
    flex-direction: column;

    &::before {
      content: none;
    }
  }
}


.contacts__phone {
  @media (max-width: 550px) {
    order: 1;
  }
}

.contacts__email {
  @media (max-width: 550px) {
    order: 2;
  }
}
